	module.exports = {"data":{"markdownRemark":{"html":"<p>C73乙でした＆あけましておめでとうございます。danmaq作品をご購入された方には深く御礼申し上げます。思えばwebサイト初めて9年、同人活動始めてから3年半(公式には3年)、結構長いようで短いようで。そんなわけで今年もよろしくお願いします。</p>\n<h2>このイラストは？</h2>\n<p>画像はコミケ中に描いてたスケブの内の1枚です(ホントはネズミ関係描きたかったが完璧に忘却の彼方だったorz) 相変わらず線が綺麗じゃないんだよなぁ。一発描きってどうにも慣れない。自宅ではいつも1枚描くたびにライトボードで5回くらいセルフトレスし直して仕上げているんだけど、この機にライトボード封印して練習してみようかな。</p>\n<h2>ちょっと大事なお知らせ</h2>\n<p>急で申し訳ないですが、<em>danmaq活動をしばらくの間休止します</em>。期間としては半年～1年程度を予定しています。まだやり残していることが結構残っているので遅くとも翌年には復活のつもりです。今まで皆様にお世話になりまして、大変ありがとうございました。</p>\n<h2>活動休止中でも継続する活動</h2>\n<p><a href=\"http://e.danmaq.com/\">danmaq個人通販“e-danmaq”</a></p>\n<ul>\n<li>過去作品の在庫生産(CD-R・コピ本などの手作り作品のみに限定)</li>\n<li>webサイトの更新および保守</li>\n<li>同人に遠く及ばない程度の極小規模な個人創作活動</li>\n</ul>","frontmatter":{"cover":null,"date":"2008-01-01T23:00:00+09:00","strDate":"2008/1/1","title":"今年はゆるーく生きたい"}}},"pathContext":{"path":"/ja/blog/2008/01/01-今年はゆるーく生きたい/","langKey":"ja"}}

